  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}
.tab-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(120, 119, 198, 0.3) 0%, rgba(255, 119, 198, 0.3) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}
.tab-button.active {
  background: linear-gradient(135deg, rgba(120, 119, 198, 0.3) 0%, rgba(255, 119, 198, 0.3) 100%);
  color: white;
  border-color: rgba(120, 119, 198, 0.4);
  box-shadow: 0 4px 16px rgba(120, 119, 198, 0.3);
}
.tab-button:hover:not(.active) {
  background: rgba(255, 255, 255, 0.15);
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(255, 255, 255, 0.1);
}
.tab-button:hover:not(.active)::before {
  opacity: 0.5;
}
.tab-button span {
  position: relative;
  z-index: 1;
}
.main-title {
  background: linear-gradient(135deg, #7877c6 0%, #ff77c6 50%, #78dbff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-size: 3.5rem;
  font-weight: 800;
  text-align: center;
  margin-bottom: 2rem;
  animation: titleGlow 3s ease-in-out infinite alternate;
  text-shadow: 0 0 30px rgba(120, 119, 198, 0.5);
}
@keyframes titleGlow {
  0% {
    filter: drop-shadow(0 0 20px rgba(120, 119, 198, 0.5));
  }
  100% {
    filter: drop-shadow(0 0 30px rgba(255, 119, 198, 0.7));
  }
}
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
}
.loading-spinner {
  width: 48px;
  height: 48px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top: 4px solid #7877c6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-text {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1rem;
  animation: pulse 2s ease-in-out infinite;
}
@keyframes pulse {
  0%, 100% {
    opacity: 0.6;
  }
  50% {
    opacity: 1;
  }
}
.error-message {
  background: rgba(255, 59, 48, 0.1);
  border: 1px solid rgba(255, 59, 48, 0.3);
  border-radius: 1rem;
  padding: 1rem 1.5rem;
  color: #ff6b6b;
  text-align: center;
  backdrop-filter: blur(10px);
  animation: shake 0.5s ease-in-out;
}
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  75% {
    transform: translateX(5px);
  }
}
.cached-indicator {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.25rem 0.75rem;
  background: rgba(52, 199, 89, 0.2);
  border: 1px solid rgba(52, 199, 89, 0.3);
  border-radius: 0.5rem;
  color: #34c759;
  font-size: 0.875rem;
  font-weight: 500;
}
.cached-indicator::before {
  content: '⚡';
  animation: flash 1.5s ease-in-out infinite;
}
@keyframes flash {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
@media (max-width: 768px) {
  .main-title {
    font-size: 2.5rem;
  }
  .form-container {
    padding: 1.5rem;
  }
  .modern-input {
    padding: 0.875rem 1.25rem;
  }
  .modern-btn {
    padding: 0.875rem 1.75rem;
  }
}
@media (max-width: 480px) {
  .main-title {
    font-size: 2rem;
  }
  .form-container {
    padding: 1rem;
  }
}
